      this.now_time.day = myDate.getDate()-1;        //获取当前日(1-31)
      this.now_time.hour = myDate.getHours();       //获取当前小时数(0-23)
      this.now_time.minute = myDate.getMinutes();     //获取当前分钟数(0-59)
      this.now_time.second = myDate.getSeconds();     //获取当前秒数(0-59)
    },
    rotate_second (x) {
      for(var i=0;i<60;i++){
        this.second[i].deg +=x;
      }
    },
    rotate_minute (x) {
      for(var i=0;i<60;i++){
        this.minute[i].deg +=x;
      }
    },
    rotate_hour (x) {
      for(var i=0;i<24;i++){
        this.hour[i].deg +=x;
      }
    },
    rotate_day (x) {
      for(var i=0;i<31;i++){
        this.day[i].deg +=x;
      }
    },
    rotate_month (x) {
      for(var i=0;i<12;i++){
        this.month[i].deg +=x;
      }
    },
    init_time(){
      this.get_now_time();
      this.rotate_month(this.now_time.month);
      this.rotate_day(this.now_time.day);
      this.rotate_hour(this.now_time.hour);
      this.rotate_minute(this.now_time.minute);
      this.rotate_second(this.now_time.second);
    },
    color_time() {
      var myDate = new Date();
      var month = myDate.getMonth();       //获取当前月份(0-11,0代表1月)
      var day = myDate.getDate()-1;        //获取当前日(1-31)
      var hour = myDate.getHours();       //获取当前小时数(0-23)
      var minute = myDate.getMinutes();     //获取当前分钟数(0-59)
      var second = myDate.getSeconds();     //获取当前秒数(0-59)
      for(var i=0;i<60;i++){
        if(this.second[i].class == "now"){
          this.second[i].class = ""
        }
      }
      this.second[59-second].class = "now";

      for(var i=0;i<60;i++){
        if(this.minute[i].class == "now"){
          this.minute[i].class = ""
        }
      }
      this.minute[59-minute].class = "now";

      for(var i=0;i<24;i++){
        if(this.hour[i].class == "now"){
          this.hour[i].class = ""
        }
      }
      this.hour[23-hour].class = "now";

      for(var i=0;i<31;i++){
        if(this.day[i].class == "now"){
          this.day[i].class = ""
        }
      }
      this.day[30-day].class = "now";

      for(var i=0;i<12;i++){
        if(this.month[i].class == "now"){
          this.month[i].class = ""
        }
      }
      this.month[11-month].class = "now";
    }
  },
  mounted () {

    this.init_time();
    var second = this.now_time.second;
    var minute = this.now_time.minute;
    var hour = this.now_time.hour;
    var day = this.now_time.day;
    var month = 0;
    setInterval(() => {
      this.color_time()
      console.log(second)
      this.rotate_second(1)
      if(second == 59){
        second = -1
        this.rotate_minute(1)
        if(minute == 59){
          minute =-1
          this.rotate_hour(1)
        }
        minute = minute+1
      }
      second = second+1
    }, 1000);

